
import CombinedVueInstance from 'vue'
import { Vue, Component } from 'vue-property-decorator'
@Component
export default class TableList extends Vue {
  $bus!: CombinedVueInstance

  col = 3
  row = 3
  determine(): void {
    this.$bus.$emit('call-painter-ready-insert-table', {
      col: this.col,
      row: this.row
    })
  }
}
